import ProjectHeader from './projectComponents/header'
import ProjectDetail from './projectComponents/detail/header'
import ProjectModuleHeader from './projectComponents/module/header'
import axios from 'axios'

export default {
  name: 'project',
  components: {
    ProjectHeader,
    ProjectDetail,
    ProjectModuleHeader
  },
  data () {
    return {
      project: Object,
      module: [],
      pName: ''
    }
  },
  methods: {
    getProject () {
      sessionStorage.setItem('projectId', this.$route.params.pId)
      axios.get(this.url + '/project/id/' + this.$route.params.pId)
        .then(this.getProjectSucc)
    },
    getProjectSucc (res) {
      if (res.status === 200) {
        const data = res.data
        this.project = data
        this.module = data.modules
        this.pName = data.name
      }
    }
  },
  created () {
    this.getProject()
  }
}



