import axios from 'axios'

export default {
  name: 'newModule',
  props: {
    leader: Array
  },
  data () {
    return {
      module: {
        name: '',
        creatorID: parseInt(localStorage.getItem('id')),
        startTime: '',
        endTime: '',
        content: '',
        leaderID: '',
        projectID: parseInt(sessionStorage.getItem('projectId')),
        target: ''
      }
    }
  },
  methods: {
    getStudentList () {
      axios.get(this.url + '/user/level/3')
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      console.log(res)
      res = res.data
      if (res) {
        this.StudentList = res
        console.log(this.StudentList)
      }
    },
    submit () {
      this.$axios({
        method: 'post',
        url: this.url + '/module/',
        data: JSON.stringify(this.module)
      }).then((module) => {
        console.log(module)
        alert('成功创建模块！')
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.getStudentList()
  }
}



