export default {
  name: 'projectHeader',
  props: {
    pName: String
  },
  data () {
    return {
      dialogVisible: false,
      level: localStorage.getItem('level')
    }
  },
  methods: {
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    deleteProject () {
      this.$axios.delete(this.url + '/project/delete' + this.$route.params.pId)
    }
  }
}



