import axios from 'axios'

export default {
  name: 'projectModule',
  data () {
    return {
      moduleList: []
    }
  },
  methods: {
    getModuleList () {
      axios.get(this.url + '/module/project/' + sessionStorage.getItem('projectId'))
        .then(this.getModuleListSucc)
    },
    getModuleListSucc (res) {
      res = res.data
      if (res) {
        this.moduleList = res
        console.log(this.moduleList)
      }
    }
  },
  created () {
    this.getModuleList()
  }
}



