import RunModule from './runModule'
import FinishedModule from './finishedModule'
import NewModule from './newModule'

export default {
  name: 'moduleHeader',
  components: {
    RunModule,
    FinishedModule,
    NewModule
  },
  props: {
    project: Object
  },
  data () {
    return {
      activeIndex: '1',
      Run: '',
      Finished: '',
      New: ''
    }
  },
  mounted: function () {
    this.Run = true
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    showRun () {
      this.Run = true
      this.New = false
      this.Finished = false
    },
    showFinished () {
      this.Finished = false
      this.Finished = true
      this.Run = false
      this.New = false
    },
    showNew () {
      this.Run = false
      this.New = false
      this.New = true
      this.Finished = false
    }
  }
}



